.product__list {
  display: flex;
  flex-direction: column;
  color: var(--black);
  padding: 200px 0 0 0;
}

.product__list__row {
  display: flex;
  align-items: center;
  background: var(--primary);
}

.product__list__column {
  cursor: pointer;
  position: relative;
  width: 100%;
  padding: 25px 40px 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  background: var(--primary);
  height: 420px;
  transition: 0.3s;
}

.product__list__column:hover .slick-arrow {
  visibility: visible;
  opacity: 1;
}

.product__list__column__image__container {
  width: 230px;
  height: 230px;
}

.product__list__column__image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.slick-track {
  display: flex !important;
  align-items: center;
}

.slick-slider {
  display: flex;
  align-items: center;
  width: 230px;
}

.slick-slide {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 5px;
}

.slick-slide img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.slick-arrow {
  visibility: hidden;
  opacity: 0;
  width: 10px !important;
  height: 15px !important;
  object-fit: contain !important;
  transition: 0.2s;
}

.product__list__column:hover {
  position: relative;
  z-index: 2;
  transform: scale(1.02);
  box-shadow: 0px 4px 19px rgba(0, 0, 0, 0.15);
  border-radius: 20px;
}

.product__list__column:hover .product__list__column__button__container {
  margin: 20px 0 0 0;
  height: 44px;
  transition: height 0.3s;
}

.product__list__column__title {
  font-size: 1.5rem;
  width: 100%;
  text-align: left;
}

.product__list__column__subheader {
  margin: 20px 0 8px;
  width: 100%;
  text-align: left;
}

.product__list__column__button__container {
  display: flex;
  flex-shrink: 0;
  width: 100%;
  height: 0;
  overflow: hidden;
  transition: height 0.3s;
}

.smaller {
  font-size: 1.2rem;
}

